
import { reactive, toRefs } from "vue";
import { Dialog, Button } from "../packages";

export default {
  name: "Dialog-example",
  components: { lanButton: Button },
  setup() {
    const state = reactive({
      minValue: 15500,
      maxValue: 25000,
    });

    const fn1 = () => {
      Dialog({ message: "你好" }); // Dialog.alert({message:"你好"})
    };

    const fn2 = () => {
      Dialog.confirm({
        title: "警告!!!",
        message: "<span style='color:orange'>真的要删除吗?</span>",
        className: "app-home-confirm",
        allowHtml: true,
        lockScroll: true,
        beforeClose: (action) => {
          if (action === "confirm") {
            return false;
          } // 点击确认按钮的时候,阻止弹出层关闭
        },
        cancelButtonText: "取消",
        confirmButtonText: "哎呦,关闭不了呢!",
      })
        .then((ret) => {
          console.log(ret); // 点击确定按钮
        })
        .catch((ret) => {
          console.log(ret); // 点击取消按钮
        });
    };
    const fn3 = () => {
      Dialog.confirm({
        message: "真的要删除吗?",
        beforeClose: (action) => {
          // 返回Promise
          return new Promise((resolve) => {
            setTimeout(() => {
              if (action === "confirm") {
                resolve(true);
              } else {
                resolve(false);
              }
            }, 1000);
          });
        },
      });
    };

    return {
      ...toRefs(state),
      fn1,
      fn2,
      fn3,
    };
  },
};
