
import { reactive, toRefs } from "vue";
import { Button } from "../packages";

export default {
  components: { lanButton: Button },
  setup() {
    const state = reactive({
      toggle: false,
    });

    const bunclick = (e: MouseEvent) => {
      alert('click')
      console.log(e);
    };

    return {
      ...toRefs(state),
      bunclick,
    };
  },
};
