
import { reactive, toRefs } from "vue";
import { Slider } from "../packages";

export default {
  components: {  Slider },
  setup() {
    const state = reactive({
      minValue: 15500,
      maxValue: 25000,
    });

    const chage = (minValue: number, maxValue: number) => {
      state.minValue = minValue;
      state.maxValue = maxValue;
    };
    const up = (e: any) => {
      console.log(e); //// e中包含了原有的e信息, 并添加了custom对象
      state.minValue = e.custom.minValue;
      state.maxValue = e.custom.maxValue;
    };
    const down = (e: any) => {
      console.log(e);
    };
    const move = (e: any) => {
      console.log(e);
    };

    return {
      ...toRefs(state),
      chage,
      up,
      move,
      down,
    };
  },
};
