<template>
  <lan-button type="success" class="btn" @click="open1('center')">普通使用</lan-button>
  <br />
  <lan-button type="success" class="btn" @click="open1('top')">顶部</lan-button>
  <br />
  <lan-button type="warning" class="btn" @click="open1('bottom')">底部</lan-button>
  <br />
  <lan-button type="warning" class="btn" @click="open2()">5秒后关闭允许html,添加背景遮罩层</lan-button>
  <br />
  <lan-button type="danger" class="btn" @click="open3('success')">成功提示,5秒后关闭</lan-button>
  <br />
  <lan-button type="danger" class="btn" @click="open3('fail')">失败提示,5秒后关闭</lan-button>
  <br />
  <lan-button type="primary" class="btn" @click="open4()">loading,3秒后关闭</lan-button>
  <br />
</template>

<script>
import { reactive, toRefs } from "vue";
import { Toast, Button } from "../packages";

export default {
  name: "toast-example",
  components: { lanButton: Button },
  setup() {
    const open1 = (type) => { Toast({ position: type, message: "你的校验不通过哦", }) }
    const open2 = () => {
      Toast({
        message: "<span style='color:orange'>你的校验不通过哦</span>",
        allowHtml: true,
        duration: 5000,
        shade: true,
      })
    }
    const open3 = (str) => {
      Toast({
        type: str,
        message: `执行${str === 'fail' ? '失败' : '成功'}！`,
        duration: 5000
      })
    }

    const open4 = () => {
      var i = Toast({
        type: "loading",
        message: "数据正在加载中"
      })

      setTimeout(() => {
        Toast.close(i)
      }, 3000)

    }
    return {
      open1,
      open2,
      open3,
      open4,
    };
  },
};
</script>



