<template>
  <div style="text-align:center">选择的时间 {{defaultDate1[0]}}</div>
  <lan-calendar
    minDate="2021-03-09"
    maxDate="2021-03-19"
    :defaultDate="defaultDate1"
    monthFormatter="YYYY-MM"
    dateFormatter="DD"
    :startWeek="0"
    @choose="choose"
  />
  <hr />
  <div style="text-align:center">
    <lan-button @click="toggle1 = !toggle1">弹窗选择的区间{{defaultDate2.join(",")}}</lan-button>
  </div>

  <Popup v-model:show="toggle1" position="bottom" :style="{ height: '60%'}">
    <lan-calendar
      minDate="2021-03-09"
      maxDate="2021-05-19"
      :defaultDate="defaultDate2"
      :isShowSectionBg="true"
      monthFormatter="YYYY月MM日"
      dateFormatter="DD日"
      @choose="dChoose"
    />
  </Popup>
</template>

<script>
import { reactive, toRefs } from "vue";
import { Calendar, Popup, Button } from "../packages";
import moment from 'moment'

export default {
  components: { lanCalendar: Calendar, Popup, lanButton: Button },
  setup() {
    const state = reactive({
      defaultDate1: ["2021-03-09"],
      defaultDate2: [],
      toggle1: false,
    })
    const choose = (date) => {
      state.defaultDate1 = [date];
    }
    const dChoose = (date) => {
      let arr = state.defaultDate2;
      if (arr.length >= 2) { arr = []; }
      arr.push(date);
      if (arr.length == 2 && moment(arr[0]).diff(moment(arr[1])) >= 0) {
        arr.shift();
      }
      state.defaultDate2 = arr;
      if (arr.length === 2) {
        state.toggle1 = false;
      }
    }
    return {
      ...toRefs(state),
      choose,
      dChoose
    }
  }
};
</script>


<style lang="less">
.lan-calendar-week {
  position: sticky;
  left: 0;
  right: 0;
  top: 0;
  background-color: #fff;
}
</style>
