
import { reactive, toRefs } from "vue";
import { Button, Popup } from "../packages";

export default {
  components: { lanButton: Button, lanPopup: Popup },
  setup() {
    const state = reactive({
      toggle1: false,
      toggle2: false,
      toggle3: false,
      toggle4: false,
      toggle5: false,
    });

    return {
      ...toRefs(state),
    };
  },
};
